import { useEffect } from "react"
import { useStores } from '../../stores/hooks'
import { useHistory, useLocation } from 'react-router'

export const useSearch = () => {
    const { mainPageStore } = useStores()
    const history = useHistory()
    const location = useLocation()

    useEffect(() => {
        try {
            const params = new URLSearchParams({
                activeTab: mainPageStore.activeTab,
                searchByName: mainPageStore.vaultsFiltersStore.searchByName,
                selectedPlatform: mainPageStore.vaultsFiltersStore.selectedPlatform,
                selectedAsset: mainPageStore.vaultsFiltersStore.selectedAsset,
                isOnlyDeposited: mainPageStore.vaultsFiltersStore.isOnlyDeposited,
                isOnlyDeactivated: mainPageStore.vaultsFiltersStore.isOnlyDeactivated,
                isNewVaultsFirst: mainPageStore.vaultsFiltersStore.isNewVaultsFirst,
                isOnlyStablecoins: mainPageStore.vaultsFiltersStore.isOnlyStablecoins,
                isOnlyBlueChips: mainPageStore.vaultsFiltersStore.isOnlyBlueChips,
                isOnlySingleVault: mainPageStore.vaultsFiltersStore.isOnlySingleVault,
                isOnlyAutocompondVaults: mainPageStore.vaultsFiltersStore.isOnlyAutocompondVaults,
                selectedSort: mainPageStore.vaultsFiltersStore.selectedSort,
            });

            history.replace({
                pathname: location.pathname,
                search: params.toString()
            });
        } catch (e) {
        }
    }, [
        mainPageStore.activeTab,
        mainPageStore.vaultsFiltersStore.isOnlyDeposited,
        mainPageStore.vaultsFiltersStore.isOnlyDeactivated,
        mainPageStore.vaultsFiltersStore.isNewVaultsFirst,
        mainPageStore.vaultsFiltersStore.isOnlyStablecoins,
        mainPageStore.vaultsFiltersStore.isOnlyBlueChips,
        mainPageStore.vaultsFiltersStore.isOnlySingleVault,
        mainPageStore.vaultsFiltersStore.isOnlyAutocompondVaults,
        mainPageStore.vaultsFiltersStore.searchByName,
        mainPageStore.vaultsFiltersStore.selectedPlatform,
        mainPageStore.vaultsFiltersStore.selectedAsset,
        mainPageStore.vaultsFiltersStore.selectedSort,
    ]);
}
